import React from 'react';

class Table extends React.Component{

	constructor(props){
		super(props);
		this.state = {
			students: [
					{ id: 1, name: 'Wasif', age: 21, email: 'wasif@email.com' },
					{ id: 2, name: 'Ali', age: 19, email: 'ali@email.com' },
					{ id: 3, name: 'Saad', age: 16, email: 'saad@email.com' },
					{ id: 4, name: 'Asad', age: 25, email: 'asad@email.com' }
				],
			autoIncr: 4
			}

		this.onAddClick = this.addRow.bind(this);
	}

	componentDidMount(){

	}

	renderTableData() {
      return this.state.students.map((student, index) => {
         const { id, name, age, email } = student //destructuring
         return (
            <tr key={id}>
               <td>{id}</td>
               <td>{name}</td>
               <td>{age}</td>
               <td>{email}</td>
            </tr>
         )
      })
   }

   addRow(){
	   	var nextId = this.setState({autoIncr: this.state.autoIncr+1});
		var data = this.state.students;
		data.push({ id: nextId, name: 'Asad', age: 25, email: 'new@email.com' });
		this.setState({students: data});
   }

   renderTableHeader() {
      let header = Object.keys(this.state.students[0])
      return header.map((key, index) => {
         return <th key={index}>{key.toUpperCase()}</th>
      })
   }

	render(){
		return(
			<div className={"WebketchTable"}>
				<h1>React Dynamic Table</h1>
				<button className={"TableAdd"} onClick={this.onAddClick}>Add</button>
				<table id='students'>
	               <tbody>
				   		<tr>{this.renderTableHeader()}</tr>
	                  	{this.renderTableData()}
	               </tbody>
	            </table>
			</div>
		);
	}
}

export default Table;
