export const logOut = () => {
    Cookies.remove(process.env.REACT_APP_sessionToken);
}



export const isAuthenticated = function () {

    var session = getSession();
    console.log('session', session);
    if (session == null) {
        return false;
    }
    if (session.creationTime == null) {
        console.log("it was response");
        return false;
    }
    var newd = new Date().getTime();
   
    if (  newd - session.creationTime > 1800000) {
        console.log("no it was",  newd, session.creationTime );
        return false;
    } else {
        console.log("ye lo",newd-session.creationTime);
        return true;
    }

}